import React, { useState } from 'react';
import Title from '../../common/Title';
import './styles.css';
import Divider from '../../common/Divider';




import obm0 from '../../../pdf/figs/obm0.jpg';
import obm1 from '../../../pdf/figs/obm1.PNG';
import obm2 from '../../../pdf/figs/obm2.PNG';
import obm3 from '../../../pdf/figs/obm3.PNG';
import obm4 from '../../../pdf/figs/obm4.PNG';

// import eduq0 from '../../../pdf/figs/eduq0.PNG';

import pedez0 from '../../../pdf/figs/pedez0.png';
import pedez1 from '../../../pdf/figs/pedez1.PNG';
import pedez2 from '../../../pdf/figs/pedez2.PNG';
import pedez3 from '../../../pdf/figs/pedez3.PNG';


import trans1 from '../../../pdf/figs/trans1.PNG';
import trans2 from '../../../pdf/figs/trans2.PNG';
import trans3 from '../../../pdf/figs/trans3.PNG';
import trans4 from '../../../pdf/figs/trans4.PNG';
import trans5 from '../../../pdf/figs/trans5.PNG';
import trans6 from '../../../pdf/figs/trans6.PNG';
import trans7 from '../../../pdf/figs/trans7.PNG';



const Education = () => {
  const [abstractIsVisible, setAbstractIsVisible] = useState(false);
  return (
    <div id="education">

      <div className="area">Educational Policy</div>


      <p>"Prêmio Escola Nota 10", Ceará </p>

      <p className="ico"><img  src={pedez0} alt="graph" /></p> 


        <span><p>Goldemberg, Diana; Bacalhau, Priscilla, Lautharte Junior, Ildo José (2020). <a href="http://documents1.worldbank.org/curated/en/509941616141843380/pdf/Can-Peer-Mentoring-Coupled-with-Incentives-Affect-School-Turnaround-Evidence-from-Ceara-State-in-Brazil.pdf"> "Parcerias com incentivos podem melhorar escolas de baixo desempenho? Evidências do estado do Ceará"</a>. Manuscrito em Português</p></span>
        <div><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Resumo do Manuscrito{abstractIsVisible ? ': ' : " (click to expand)"}</span> {abstractIsVisible ? "''Esta nota apresenta o Prêmio Escola Nota 10, um programa de mentoria entre diretores e equipes escolares no estado Ceará, Brasil, e discute as evidências de seu impacto nos resultados de aprendizagem dos alunos. O programa forma pares entre escolas com baixo desempenho acadêmico e escolas de alto desempenho, e ambas recebem uma pequena contribuição financeira incondicional. Para incentivar a participação, as duas escolas recebem um segundo desembolso financeiro, condicional à posterior melhoria da escola de baixo desempenho em exames padronizados. Explorando o desenho do programa, usa-se o método de regressões descontínuas para estimar o seu impacto. Os resultados indicam que as escolas de baixo desempenho que participam do programa melhoram seu desempenho escolar em 0,18 desvio padrão no ano seguinte. Os efeitos sobre escolas participantes de baixo desempenho estão positivamente associados à similaridade no número de alunos matriculados e à proximidade geográfica entre as escolas pareadas. A participação não causa qualquer efeito expressivo nas escolas de alto desempenho. Essas conclusões mostram que é possível promover a recuperação de escolas com baixo desempenho, sistemicamente e em escala, sem precisar substituir a gestão escolar.''": ''}
      </div>


      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figura 1 do Manuscrito, Componentes do Prêmio Escola Nota 10{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={pedez3} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figura 2 do Manuscrito, Amostra Principal de Escolas{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={pedez1} alt="graph" /></p>: ''}
      </div>

      
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figura 3 do Manuscrito, Efeito Local Médio do Programa sobre Indicador Educacional{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={pedez2} alt="graph" /></p>: ''}
      </div>

      <p>Eduq+, São Paulo </p>


        <span><p>Goldemberg, Diana; Bacalhau, Priscilla, Lautharte Junior, Ildo José (2020). <a href="http://documents1.worldbank.org/curated/en/509941616141843380/pdf/Can-Peer-Mentoring-Coupled-with-Incentives-Affect-School-Turnaround-Evidence-from-Ceara-State-in-Brazil.pdf"> "Parcerias com incentivos podem melhorar escolas de baixo desempenho? Evidências do estado do Ceará"</a>. Manuscrito em Português</p></span>
        <div><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Resumo do Manuscrito{abstractIsVisible ? ': ' : " (click to expand)"}</span> {abstractIsVisible ? "''Esta nota apresenta o Prêmio Escola Nota 10, um programa de mentoria entre diretores e equipes escolares no estado Ceará, Brasil, e discute as evidências de seu impacto nos resultados de aprendizagem dos alunos. O programa forma pares entre escolas com baixo desempenho acadêmico e escolas de alto desempenho, e ambas recebem uma pequena contribuição financeira incondicional. Para incentivar a participação, as duas escolas recebem um segundo desembolso financeiro, condicional à posterior melhoria da escola de baixo desempenho em exames padronizados. Explorando o desenho do programa, usa-se o método de regressões descontínuas para estimar o seu impacto. Os resultados indicam que as escolas de baixo desempenho que participam do programa melhoram seu desempenho escolar em 0,18 desvio padrão no ano seguinte. Os efeitos sobre escolas participantes de baixo desempenho estão positivamente associados à similaridade no número de alunos matriculados e à proximidade geográfica entre as escolas pareadas. A participação não causa qualquer efeito expressivo nas escolas de alto desempenho. Essas conclusões mostram que é possível promover a recuperação de escolas com baixo desempenho, sistemicamente e em escala, sem precisar substituir a gestão escolar.''": ''}
      </div>


      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figura 1 do Manuscrito, Componentes do Prêmio Escola Nota 10{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={pedez3} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figura 2 do Manuscrito, Amostra Principal de Escolas{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={pedez1} alt="graph" /></p>: ''}
      </div>

      
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figura 3 do Manuscrito, Efeito Local Médio do Programa sobre Indicador Educacional{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={pedez2} alt="graph" /></p>: ''}
      </div>


      <p>"Olimpíada Brasileira de Matemática" (OBM) </p>

      <p className="ico"><img  src={obm0} alt="graph" /></p> 


        <span><p><a href="https://dianamoreira.com/" target="blank">Moreira, Diana</a> (2020). <a href="https://dianamoreira.com/pdf/dm/Moreira_MO.pdf"> "Success Spills Over: How Awards Affect Winners' and Peers' Performance in Brazil"</a>. Manuscript, Previously circulated as: "Recognizing Performance".</p></span>
        <div><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Manuscript Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span> {abstractIsVisible ? "''Does the recognition of someone’s accomplishment spill over to others? Awards that confer public recognition for outstanding performance can impact ex-post behavior by changing beliefs, norms or interests. I investigate whether the public recognition of students’ accomplishments impacts their own and their peers’ subsequent academic performance. I exploit Brazil’s Math Olympiad Honorable Mention award which recognizes the top 4% of participants in a national competition involving 18 million students annually. I take advantage of the fact that no information is disclosed on the performance of those who do not win an award to recover the informational impact of someone’s recognition. Specifically, I use a regression discontinuity design comparing classrooms with narrow winners and losers of the award. I find that the award improves the future educational outcomes of both the winner and her classmates. The spillovers on classmates are economically meaningful - one-fifth of the magnitude of the effects on the winner themselves - and have long-run consequences: the enrollment in selective colleges of classmates of a narrow award winner increases by 10%. Proximity to the winner, both physical and in terms of ability, appears to be a key mediating channel: spillovers are largest for classmates in the top quartile of the test score distribution, and depend upon the presence of the winner in the classroom. The results show that ex-post motivation and effort can be enhanced by recognizing the performance of a high-achieving student.''": ''}
      </div>


      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 2 from Manuscript, Average Number of Classrooms per School in the Main Analytical Sample{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={obm1} alt="graph" /></p>: ''}
      </div>

      
      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 10 from Manuscript, Local Average Effect on Classmates' OBM Participation in the Short Future Following an Award in the Class{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={obm2} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 11 from Manuscript, Local Average Effect on Classmates' OBM Performance in the Short Future Following an Award in the Class{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={obm3} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 13 from Manuscript, Local Average Effect on Classmates' in the Long Future Following an Award in the Class{abstractIsVisible ? ': ' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={obm4} alt="graph" /></p>: ''}
      </div>

      <Divider />


      <div className="area">Educational Effects of...</div>


      <p>Local Political and Bureaucratic Transitions</p>

        <span><p>Akhtari, Mitra; <a href="https://dianamoreira.com/" target="blank">Moreira, Diana</a> and Trucco, Laura (2020). <a href="https://dianamoreira.com/pdf/dm/Akhtari%20Moreira%20Trucco_updated.pdf"> "Political Turnover, Bureaucratic Turnover, and the Quality of Public Services"</a>. Manuscript.</p></span>
        <div><span className="linkexp" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Paper Abstract{abstractIsVisible ? ': ' : " (click to expand)"}</span> {abstractIsVisible ? "''We study how political turnover in mayoral elections in Brazil affects public service provision by local governments. Exploiting a regression discontinuity design for close elections, we find that municipalities with a new party in office experience upheavals in the municipal bureaucracy: new personnel are appointed across multiple service sectors, and at both managerial and non-managerial levels. In education, the increase in the replacement rate of personnel in schools controlled by the municipal government is accompanied by test scores that are .05-.08 standard deviations lower. In contrast, turnover of the mayor’s party does not impact local (non-municipal) schools. These findings suggest that political turnover can adversely affect the quality of public services when the bureaucracy is not shielded from the political process''": ''}
      </div>


      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 1 from Manuscript, Local Average Effect of Political Transition on{abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans1} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 4 from Manuscript, Local Average Effect of PMM on Number of Doctor , per 1,000 Residents {abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans2} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 6, Panel B, from Manuscript, Local Average Effect of PMM on Prenatal Visits by Physicians {abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans3} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 6, Panel C, from Manuscript, Local Average Effects of PMM on Prenatal Visits by Nurses {abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans4} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 7, Panel B, from Manuscript, Local Average Effects of PMM on Share of Low Birth Weights {abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans5} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 7, Panel C, from Manuscript, Local Average Effects of PMM on Infant Mortality Rates{abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans6} alt="graph" /></p>: ''}
      </div>

      <div><span className="link" onClick={() => setAbstractIsVisible(!abstractIsVisible)}>Figure 7, Panel C, from Manuscript, Local Average Effects of PMM on Infant Mortality Rates{abstractIsVisible ? '' : " (click to expand)"}</span>
      {abstractIsVisible ?   <p><img src={trans7} alt="graph" /></p>: ''}
      </div>



    </div>
    
  )
}

export default Education;
